import React, { useState } from "react";
import {
  Text,
  Spacer,
  Button,
  useTheme,
  Code,
  Switch,
  Snippet,
} from "@nextui-org/react";
import SyntaxHighlighter from "react-syntax-highlighter";
import { dark } from "react-syntax-highlighter/dist/esm/styles/hljs";

const esmCode = `import { WelcomeCard } from "@k1-1960/discord-jimp";

const card = new WelcomeCard({
  username: "K1_1960",
  title: "Bienvenid@",
  subtitle: "Ahora somos 15 miembros!",
  avatar:
    "https://cdn.discordapp.com/avatars/838091364344397835/84ad1de4bb1b2a485a2a4584e4804efd.png?size=2048",
  background:
    "https://cdn.discordapp.com/attachments/1122455580381028402/1122456268683096167/29364-1920x1080.jpg",
});

async function main() {
  await card.build();
  await card.save("esm-test/result.jpg");
}

main();`;

const cjsCode = `const { WelcomeCard } = require("../cjs/index.js");

const card = new WelcomeCard({
  username: "K1_1960",
  title: "Bienvenid@",
  subtitle: "Ahora somos 15 miembros!",
  avatar:
    "https://cdn.discordapp.com/avatars/838091364344397835/84ad1de4bb1b2a485a2a4584e4804efd.png?size=2048",
  background:
    "https://cdn.discordapp.com/attachments/1122455580381028402/1122456268683096167/29364-1920x1080.jpg",
});

async function main() {
  await card.build();
  await card.save("cjs-test/result.jpg");
}

main();`;

const ExamplesPage = () => {
  let { theme } = useTheme();

  const Component = ({ code }) => {
    const codeString = code;
    return (
      <SyntaxHighlighter
        language="javascript"
        style={dark}
        customStyle={{
          background: theme.colors.background.value,
          wordWrap: "wrap",
          width: "80vw",
        }}
        showLineNumbers={true}
        wrapLines={true}
      >
        {codeString}
      </SyntaxHighlighter>
    );
  };
  const [type, setType] = useState("esm");
  return (
    <div
      style={{
        margin: "10px",
      }}
    >
      <div
        style={{
          display: "flex",
          flexDirection: "row",
          alignItems: "center",
          justifyContent: "center",
        }}
      >
        <Text size={24}>ESM</Text> <Spacer x={1} />
        <Switch
          onChange={({ target }) => {
            target.checked ? setType("cjs") : setType("esm");
          }}
        />{" "}
        <Spacer x={1} /> <Text size={24}>CJS</Text>
      </div>
      {type === "esm" ? (
        <div
          onClick={() => {
            navigator.clipboard.writeText(esmCode);
          }}
        >
          <Component code={esmCode} />
        </div>
      ) : (
        <div
          onClick={() => {
            navigator.clipboard.writeText(cjsCode);
          }}
        >
          <Component code={cjsCode} />
        </div>
      )}
    </div>
  );
};

export default ExamplesPage;
