import React from "react";
import {
  Navbar,
  Button,
  Text,
  Link,
  Spacer,
  Image,
  useTheme,
  Switch,
} from "@nextui-org/react";
import Sun from "../images/sun.png";
import Moon from "../images/moon.png";
import { useTheme as useNextTheme } from "next-themes";

function NavbarComponent() {
  const { setTheme } = useNextTheme();
  const { isDark, type } = useTheme();

  const actualPath = window.location.pathname;
  const authenticated = localStorage.getItem("discord-auth-token") || false;
  const navbarItems = [
    {
      key: "Overview",
      href: "/",
      disabled: false,
    },
    {
      key: "Examples",
      href: "/examples",
      disabled: false,
    },
  ];

  return (
    <Navbar isBordered variant="sticky">
      <Navbar.Brand>
        <Navbar.Toggle showIn="sm" aria-label="toggle navigation" />
        <Spacer x={1} />
        <Text b>{document.title}</Text>
      </Navbar.Brand>
      <Navbar.Content>
        <Switch
          checked={true}
          size="xl"
          iconOn={<img src={Moon} width="16px" />}
          iconOff={<img src={Sun} width="16px" />}
          onChange={(e) => {
            setTheme(e.target.checked ? "dark" : "light");
          }}
        />
      </Navbar.Content>
      <Navbar.Collapse>
        {navbarItems.map((item) => (
          <Navbar.CollapseItem
            activeColor="inherit"
            key={item.key.toLowerCase()}
            isActive={actualPath === item.href}
          >
            <Link color="inherit" href={item.href}>
              {item.key}
            </Link>
          </Navbar.CollapseItem>
        ))}
      </Navbar.Collapse>
    </Navbar>
  );
}

export default NavbarComponent;
